
import React, { Component } from 'react';
import { Dropdown } from 'react-bootstrap';
import ReactMultiSelectCheckboxes from 'react-multiselect-checkboxes';
import { getCollectionList } from '../../../../../../Services/Actions/homeActions'



class ChooseCollection extends Component {
    state = { postType: null, options: [] }

    componentDidMount() {
        this.onLoadCollectionList()
    }
    changeFeedsFilters = (event) => {
        const { analyticsStatus } = this.props;
        this.setState({ postType: event });
        this.props.updateCollectionList(event);
    }
    onLoadCollectionList = () => {
        var self = this;
        this.setState({ collectionListRequest: true })
        getCollectionList().then((response) => {
            const { responseData } = response.data;
            if (responseData && Object.keys(responseData).length > 0) {
                var options = []
                responseData.collection.forEach((item) => {
                    options.push({ value: item.id, label: item.name })
                })
                self.setState({ options: options })
            }
            else {
                self.setState({ collectionListRequest: false })
            }
        }).catch((ex) => {
            self.setState({ collectionListRequest: false })
        })

    }
    render() {
        const { options, postType } = this.state
        const {isMulti} = this.props;
        return <ReactMultiSelectCheckboxes id="postype" style={{ width: 300 }}
            className="mb-3"
            classNamePrefix={`check-feed`}
            value={postType}
            isMulti={isMulti?true:false}
            onChange={this.changeFeedsFilters}
            options={options}
        />


    }
}

export default ChooseCollection;

