import React, { Component } from "react";
import {
  Modal,
  Dropdown,
  InputGroup,
  DropdownButton,
  Form,
  OverlayTrigger,
  Tooltip,
  Collapse,
} from "react-bootstrap";
import CreatableSelect from "react-select/creatable";
import SelectedNetwork from "./SelectedNetwork";
import SelectedFeed from "./SelectedFeed";
import ChooseCollection from "../../MyAssets/AddAssetsModal/Compontnets/Elements/ChooseCollection";
import { connect } from "react-redux";
import { addRule } from "../../../../Services/Actions/rulesAction";

const initialState= {
    ruleOn: "1",
    advanceOption: "1",
    name: "",
    action: "Copy",
    text: "",
    tag:[],
    collectionId: 0,
    selectedNetworkList:[],
    selectedFeedsList:[],
    validateName:false,
    validateCollection:false,
    validateAction:false,
    validateAdvOpt:false
  };

class CreateEditRuleModal extends Component {
  constructor() {
    super();
    this.state = initialState;
  }

  advanceOpFun = (event) => {
    if(event.target.value==="1"){
        this.setState((prev) => ({ ...prev, selectedFeedsList: [],selectedNetworkList: [] }));
    }else if(event.target.value==="2"){
        this.setState((prev) => ({ ...prev, selectedFeedsList: [] }));
    }else if(event.target.value==="3"){
        this.setState((prev) => ({ ...prev, selectedNetworkList: [] }));
    }

    this.setState((prev) => ({
      ...prev,
      advanceOption: event.target.value
    }));
    this.hideValidation();
  };

  handleOnChange = (event) => {
    this.setState((prev) => ({
      ...prev,
      [event.target.name]: event.target.value,
    }));
    this.hideValidation();
  };

  handleDropDownValue = (value) => {
    this.setState((prev) => ({ ...prev, action: value }));
    this.hideValidation();
  };

  

  


  handleChangeMultiSelect = (selectedOption) => {
    this.setState((prev) => ({ ...prev, tag: selectedOption }));
    this.hideValidation();
  };

  updateNetworkList = (id) => {
    const {selectedNetworkList} = this.state;
    if (selectedNetworkList.indexOf(id) !== -1) {
      const list = selectedNetworkList.filter((networkId) => networkId !== id);
      this.setState((prev) => ({ ...prev, selectedNetworkList: list }));
    } else {
        this.setState((prev) => ({ ...prev, selectedNetworkList: [...this.state.selectedNetworkList,id] }));
    }
    this.hideValidation();
  };

  updateFeedList = (selectedFeedList) => {
    this.setState((prev) => ({ ...prev, selectedFeedsList: selectedFeedList }));
    this.hideValidation();
  };



  updateCollectionList = (selectedList) => {
    this.setState((prevState) => ({
      ...prevState,
      collectionId: selectedList.value,
    }));
    this.hideValidation();
  };

  convertArrToString = (list,isDestructingReq=false) => {
    if(list.length>0 && !isDestructingReq){
    const string = list.toString();
    return string;
    } else if(list.length>0 && isDestructingReq){
    const destructedArr = list.map((ele)=>ele.value);
    return destructedArr.toString();
    }
    return "";
  };

  validatingAdvanceOption(){
    const {advanceOption,selectedNetworkList,selectedFeedsList} = this.state;
    if(advanceOption==="2" && selectedNetworkList.length===0){
       return true
    }else if(advanceOption==="3" && selectedFeedsList.length===0){
       return true 
    } 
    return false;
  }

  validatingAction(){
    const {ruleOn,text,tag } = this.state;
    
    if(ruleOn==="1" && text===''){
       return true
    }else if(ruleOn==="2" && text===''){
       return true 
    } else if(ruleOn==="3" && text===''){
       return true 
    }else if(ruleOn==="4" && tag.length===0){
        return true 
    }
    return false;
  }

  hideValidation(){
    const {validateAction,validateCollection,validateAdvOpt,validateName} = this.state;
   if(validateAdvOpt || validateName ||validateAction || validateCollection){
    this.setState((prev) => ({ ...prev, validateAdvOpt: false,validateName:false,validateAction:false,validateCollection:false }));
   }
  }

  handleCloseModal=()=>{
    this.props.createEditCallback(false);
    this.setState(initialState);
  }

  handleFormSubmission = () => {
    const {name,collectionId } = this.state;
    if(name===""||name.length<=1){
    this.setState((prev) => ({ ...prev, validateName: true }));
    }else if(collectionId===0){
    this.setState((prev) => ({ ...prev, validateCollection: true }));
    }else if(this.validatingAction()){
        this.setState((prev) => ({ ...prev, validateAction: true }));
    }else if(this.validatingAdvanceOption()){
        this.setState((prev) => ({ ...prev, validateAdvOpt: true }));
    }else {
        const {addNewRule} = this.props;
        const postbody = this.creatingPostBody();
   
        //call api
        addNewRule(postbody);
    }
  };

  creatingPostBody(){
    const {name,collectionId,advanceOption,action,text,ruleOn,selectedFeedsList,selectedNetworkList,tag} = this.state;
    const postbody= {
     name:name,
     collection:Number(collectionId),
     target:Number(advanceOption),
     action:Number(action==="Copy"?"5":"4"),
     text:ruleOn==="4"?this.convertArrToString(tag,true):text,
     filter:Number(ruleOn),
     feeds:this.convertArrToString(selectedFeedsList),
     networks:this.convertArrToString(selectedNetworkList),
     tags:action!=="Copy"?text:''
 }
  
 if(postbody.action===5){
    postbody.tags=''
 }else {
    postbody.text='';
 }
     
     return postbody;
   }



  render() {
    const { createEditToggle, networkList,tags } = this.props;
    const { ruleOn, name, text, action, advanceOption,selectedNetworkList,selectedFeedsList,validateAction,validateName,validateCollection,validateAdvOpt } = this.state;
    return (
      <Modal
        show={createEditToggle}
        onHide={()=>this.handleCloseModal()}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title>Add New Rule</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="mx-xl-4 px-2 px-lg-4 mx-0 pt-4">
            <div className="row mb-3 mb-xl-4">
              <label className="col-sm-3 text-gray-800 mb-2">Rule Name <strong className="text-danger">*</strong></label>
              <div className="col-sm-9">
                <input
                  type="text"
                  name="name"
                  className={`form-control ${validateName?"is-invalid":""}`}
                  placeholder="Enter rule nameâ€¦"
                  value={name}
                  onChange={this.handleOnChange}
                />
              <div className="invalid-feedback">Please provide a name.</div>
              </div>
            </div>
            <div className="row mb-3 mb-xl-4">
              <label className="col-sm-3 text-gray-800 mb-2">
                Select Collection <strong className="text-danger">*</strong>
              </label>
              <div className="col-sm-9">
                <div className={`${validateCollection?"is-invalid":""}`}>
                <ChooseCollection
                  updateCollectionList={this.updateCollectionList}
                /></div>
                <div className="invalid-feedback">Please provide a name.</div>
              </div>
            </div>
            <div className="row mb-3 mb-xl-4">
              <label className="col-sm-3 text-gray-800 mb-2">Take Action <strong className="text-danger">*</strong></label>
              <div className="col-sm-9">
                <div
                  className="d-flex align-items-center flex-wrap"
                  onChange={this.handleOnChange}
                >
                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="ruleOn"
                      value="1"
                      id="byHandle"
                      defaultChecked
                    />
                    <label className="form-check-label" htmlFor="byHandle">
                      By Handle
                    </label>
                  </div>

                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="ruleOn"
                      value="2"
                      id="byHashtag"
                    />
                    <label className="form-check-label" htmlFor="byHashtag">
                      By Hashtag
                    </label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="ruleOn"
                      value="3"
                      id="byWord"
                    />
                    <label className="form-check-label" htmlFor="byWord">
                      By Word
                    </label>
                  </div>
                  <div className="form-check">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="ruleOn"
                      value="4"
                      id="byTags"
                    />
                    <label className="form-check-label" htmlFor="byTags">
                      By Tags
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mb-3 mb-xl-4">
              <div className="offset-sm-3 col-sm-9">
                {ruleOn !== "4"?<div
                  className="position-relative w-100 mb-3"
                  style={{ zIndex: 0 }}
                >
                  {ruleOn !== "1"?<span
                    className="h-38px w-38px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-3 ps-1"
                    style={{ zIndex: 9 }}
                  >
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 24 24"
                      width="24"
                      height="24"
                    >
                      <path fill="none" d="M0 0h24v24H0z" />
                      <path
                        d="M20 12a8 8 0 1 0-3.562 6.657l1.11 1.664A9.953 9.953 0 0 1 12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10v1.5a3.5 3.5 0 0 1-6.396 1.966A5 5 0 1 1 15 8H17v5.5a1.5 1.5 0 0 0 3 0V12zm-8-3a3 3 0 1 0 0 6 3 3 0 0 0 0-6z"
                        fill="currentColor"
                      />
                    </svg>
                  </span>:ruleOn==="2"?<span className="h-38px w-38px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-3 ps-1" style={{zIndex:9}}>
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z"/>
                                                <path d="M7.784 14l.42-4H4V8h4.415l.525-5h2.011l-.525 5h3.989l.525-5h2.011l-.525 5H20v2h-3.784l-.42 4H20v2h-4.415l-.525 5h-2.011l.525-5H9.585l-.525 5H7.049l.525-5H4v-2h3.784zm2.011 0h3.99l.42-4h-3.99l-.42 4z" fill="currentColor"/>
                                            </svg>
                                        </span>:<span className="h-38px w-38px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-3 ps-1" style={{zIndex:9}}>
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z"/>
                                                <path d="M7.784 14l.42-4H4V8h4.415l.525-5h2.011l-.525 5h3.989l.525-5h2.011l-.525 5H20v2h-3.784l-.42 4H20v2h-4.415l-.525 5h-2.011l.525-5H9.585l-.525 5H7.049l.525-5H4v-2h3.784zm2.011 0h3.99l.42-4h-3.99l-.42 4z" fill="currentColor"/>
                                            </svg>
                                        </span>}
                  {/* <span className="h-38px w-38px block-center svg-icon svg-icon-4 text-gray-500 position-absolute top-0 start-0 translate-middle-x ms-3 ps-1" style={{zIndex:9}}>
                                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="24" height="24">
                                                <path fill="none" d="M0 0h24v24H0z"/>
                                                <path d="M7.784 14l.42-4H4V8h4.415l.525-5h2.011l-.525 5h3.989l.525-5h2.011l-.525 5H20v2h-3.784l-.42 4H20v2h-4.415l-.525 5h-2.011l.525-5H9.585l-.525 5H7.049l.525-5H4v-2h3.784zm2.011 0h3.99l.42-4h-3.99l-.42 4z" fill="currentColor"/>
                                            </svg>
                                        </span> */}
                  <InputGroup className={`${validateAction?"is-invalid":""}`}>
                    <Form.Control
                      aria-label="Text input with dropdown button"
                      style={{ paddingLeft: 32 }}
                      name="text"
                      onChange={(e)=>this.handleOnChange(e)}
                      value={text}
                    />
                    <DropdownButton variant="outline-secondary" title={action}>
                      <Dropdown.Item
                        onClick={() => this.handleDropDownValue("Copy")}
                      >
                        Copy
                      </Dropdown.Item>
                      <Dropdown.Item
                        onClick={() => this.handleDropDownValue("Tag")}
                      >
                        Tag
                      </Dropdown.Item>
                    </DropdownButton>
                  </InputGroup>
                    <div className="invalid-feedback">Please provide a name.</div>
                </div>:
                <Collapse in={true }>
                  <div>
                    <div className={`${validateAction?"is-invalid":""}`}>
                    <InputGroup className={`${validateAction?"is-invalid":""}`}>
                    <CreatableSelect
                      isMulti
                      options={tags.tagList.map((tag)=>({value:tag,label:tag}))}
                      onChange={(event) => this.handleChangeMultiSelect(event)}
                    />
                    <DropdownButton variant="outline-secondary" title={"Copy"}>
                      <Dropdown.Item
                        onClick={() => this.handleDropDownValue("Copy")}
                      >
                        Copy
                      </Dropdown.Item>
                    </DropdownButton>
                  </InputGroup>
                    
                    </div>
                    <div className="invalid-feedback">Please provide a name.</div>
                  </div>
                </Collapse>}
              </div>
            </div>
            <div className="row mb-3 mb-xl-4">
              <label className="col-sm-3 text-gray-800 mb-2 d-flex">
                Advance Options <strong className="text-danger">*</strong>
                <OverlayTrigger
                  placement="top"
                  overlay={
                    <Tooltip id="button-tooltip-2">
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    </Tooltip>
                  }
                >
                  <span className="svg-icon svg-icon-6 ms-1">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      viewBox="0 0 24 24"
                      width="24"
                      height="24"
                    >
                      <path fill="none" d="M0 0h24v24H0z" />
                      <path
                        d="M12 22C6.477 22 2 17.523 2 12S6.477 2 12 2s10 4.477 10 10-4.477 10-10 10zm-1-11v6h2v-6h-2zm0-4v2h2V7h-2z"
                        fill="currentColor"
                      />
                    </svg>
                  </span>
                </OverlayTrigger>
              </label>
              <div className="col-sm-9">
                <div
                  className="d-flex align-items-center flex-wrap"
                  onChange={this.advanceOpFun}
                >
                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="advanceOpt"
                      value="1"
                      id="allFeedNetwork"
                      defaultChecked
                    />
                    <label
                      className="form-check-label"
                      htmlFor="allFeedNetwork"
                    >
                      All Assets
                    </label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="advanceOpt"
                      value="2"
                      id="selectedNetwork"
                    />
                    <label
                      className="form-check-label"
                      htmlFor="selectedNetwork"
                    >
                      Selected Networks
                    </label>
                  </div>
                  <div className="form-check me-2 me-sm-3">
                    <input
                      className="form-check-input"
                      type="radio"
                      name="advanceOpt"
                      value="3"
                      id="selectedFeed"
                    />
                    <label className="form-check-label" htmlFor="selectedFeed">
                      Selected Feeds
                    </label>
                  </div>
                </div>
              </div>
            </div>
            <div className="row mb-3 mb-xl-4">
              <div className="col-sm-12">
                <Collapse in={advanceOption === "2" ? true : false}>
                 <div>
                 {advanceOption === "2"?  <SelectedNetwork
                      networkList={networkList}
                      updateNetworkList={this.updateNetworkList}
                      selectedField={selectedNetworkList}
                    />:null}
                  </div>
                </Collapse>
                <Collapse 
                in={advanceOption === "3" ? true : false}
                >
                   <div>
                   {advanceOption === "3"?  <SelectedFeed updateFeedList={this.updateFeedList} selectedFeedsList={selectedFeedsList}/>:null}
                  </div>
                </Collapse>
              </div>
            </div>
            {validateAdvOpt?<p>Please select a value</p>:null}
          </div>

        </Modal.Body>
        <Modal.Footer className="p-3">
          <div className="mx-xl-4 px-2 px-lg-4 mx-0">
            <button
              type="button"
              className="btn btn-secondary btn-sm btn-min-w me-3"
              onClick={()=>this.handleCloseModal()}
            >
              Cancel
            </button>
            <button
              type="button"
              className="btn btn-primary btn-sm btn-min-w"
              onClick={this.handleFormSubmission}
            >
              Add Rule
            </button>
          </div>
        </Modal.Footer>
      </Modal>
    );
  }
}
const mapStateToProps = state=>{
    return {
        tags:state.tags
    }
}

const mapDispatchToProps=dispatch=>({
    addNewRule:rule=>dispatch(addRule(rule))
})

export default connect(mapStateToProps,mapDispatchToProps)(CreateEditRuleModal);
