import React, { Component } from 'react';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON } from '../../../../Services/Utils/utils';

class SelectedNetwork extends Component {
    render() {
        const {networkList,updateNetworkList,selectedField} = this.props;
        return  <div className="row gx-1">
                    {
                        networkList.length>0?networkList.map((network)=>{
                        const {name,id} = network;
                           return <div className="col-sm-4 col-lg-3" key={id}>
                       <div className="d-flex align-items-start justify-content-between">
                           <label className="form-check mb-3 flex-grow-1">
                               <input className="form-check-input" type="checkbox" name="" value="instagram" onChange={()=>updateNetworkList(id)} checked={selectedField.indexOf(id)!==-1}/>
                               <span className="form-check-label d-flex align-items-center">
                               <img className="img-fluid h-100 me-1" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(id)}.svg`)} height={16} width={16} alt="" />
                                   {name}</span>
                           </label>
                       </div>
                   </div>
                       }):null
                    }
                </div>

    }
}

export default SelectedNetwork;